/* Process页面美化样式 */

/* 容器样式 */
.process-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* 流程卡片基础样式 */
.process-card {
  background-color: white;
  border-radius: 0.5rem;
  padding: 1.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border: 1px solid #f3f4f6;
}

/* 卡片标题样式 */
.process-title {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
}

.process-title-icon {
  background-color: var(--blue-hover);
  padding: 0.5rem;
  border-radius: 0.5rem;
  margin-right: 0.75rem;
}

.process-title-icon svg {
  width: 1.25rem;
  height: 1.25rem;
  color: var(--light-blue);
}

/* 采购流程样式 */
.procurement-steps {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.procurement-step {
  position: relative;
  display: flex;
  align-items: center;
  padding: 1rem;
  border-radius: 0.75rem;
  cursor: pointer;
  transition: all 0.3s ease;
  transform: translateY(0);
  border-left: 4px solid;
}

/* 不同颜色主题的采购步骤 */
.procurement-step.blue {
  background: linear-gradient(to right, #eff6ff, white);
  border-left-color: #60a5fa;
}

.procurement-step.green {
  background: linear-gradient(to right, #f0fdf4, white);
  border-left-color: #4ade80;
}

.procurement-step.purple {
  background: linear-gradient(to right, #faf5ff, white);
  border-left-color: #a855f7;
}

.procurement-step.orange {
  background: linear-gradient(to right, #fff7ed, white);
  border-left-color: #fb923c;
}

.procurement-step.indigo {
  background: linear-gradient(to right, #eef2ff, white);
  border-left-color: #6366f1;
}

.procurement-step.red {
  background: linear-gradient(to right, #fef2f2, white);
  border-left-color: #ef4444;
}

/* 悬停效果 */
.procurement-step:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transform: translateY(-4px);
}

/* 步骤图标 */
.step-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  margin-right: 1rem;
  transition: background-color 0.3s ease;
}

.step-icon.blue {
  background-color: var(--blue-hover);
}

.step-icon.green {
  background-color: #dcfce7;
}

.step-icon.purple {
  background-color: #f3e8ff;
}

.step-icon.orange {
  background-color: #fed7aa;
}

.step-icon.indigo {
  background-color: #e0e7ff;
}

.step-icon.red {
  background-color: #fecaca;
}

.procurement-step:hover .step-icon.blue {
  background-color: #bfdbfe;
}

.procurement-step:hover .step-icon.green {
  background-color: #bbf7d0;
}

.procurement-step:hover .step-icon.purple {
  background-color: #e9d5ff;
}

.procurement-step:hover .step-icon.orange {
  background-color: #fdba74;
}

.procurement-step:hover .step-icon.indigo {
  background-color: #c7d2fe;
}

.procurement-step:hover .step-icon.red {
  background-color: #fca5a5;
}

.step-icon svg {
  width: 1.25rem;
  height: 1.25rem;
}

.step-icon.blue svg {
  color: var(--light-blue);
}

.step-icon.green svg {
  color: #16a34a;
}

.step-icon.purple svg {
  color: #9333ea;
}

.step-icon.orange svg {
  color: #ea580c;
}

.step-icon.indigo svg {
  color: #4f46e5;
}

.step-icon.red svg {
  color: #dc2626;
}

/* 步骤内容 */
.step-content {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.step-title {
  font-weight: 600;
  color: #1f2937;
  transition: color 0.3s ease;
}

.procurement-step:hover .step-title {
  color: #111827;
}

.step-action {
  display: flex;
  align-items: center;
  transition: color 0.3s ease;
}

.step-action.blue {
  color: var(--light-blue);
}

.step-action.green {
  color: #16a34a;
}

.step-action.purple {
  color: #9333ea;
}

.step-action.orange {
  color: #ea580c;
}

.step-action.indigo {
  color: #4f46e5;
}

.step-action.red {
  color: #dc2626;
}

.step-detail-text {
  font-size: 0.875rem;
  font-weight: 500;
  margin-right: 0.5rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.procurement-step:hover .step-detail-text {
  opacity: 1;
}

.step-arrow {
  width: 1.25rem;
  height: 1.25rem;
  transition: transform 0.3s ease;
}

.procurement-step:hover .step-arrow {
  transform: translateX(4px);
}

/* 渐变背景覆盖层 */
.step-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 0.75rem;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.step-overlay.blue {
  background: linear-gradient(to right, #dbeafe, transparent);
}

.step-overlay.green {
  background: linear-gradient(to right, #dcfce7, transparent);
}

.step-overlay.purple {
  background: linear-gradient(to right, #f3e8ff, transparent);
}

.step-overlay.orange {
  background: linear-gradient(to right, #fed7aa, transparent);
}

.step-overlay.indigo {
  background: linear-gradient(to right, #e0e7ff, transparent);
}

.step-overlay.red {
  background: linear-gradient(to right, #fecaca, transparent);
}

.procurement-step:hover .step-overlay {
  opacity: 0.2;
}

/* 其他流程样式 */
.other-processes {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.other-process-title {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
}

.other-process-title-icon {
  background-color: #f3f4f6;
  padding: 0.5rem;
  border-radius: 0.5rem;
  margin-right: 0.75rem;
}

.other-process-title-icon svg {
  width: 1.25rem;
  height: 1.25rem;
  color: #4b5563;
}

.other-process-item {
  position: relative;
  display: flex;
  align-items: flex-start;
  padding: 1.25rem;
  border-radius: 0.75rem;
  cursor: pointer;
  transition: all 0.3s ease;
  border: 1px solid;
}

.other-process-item.emerald {
  background: linear-gradient(135deg, #ecfdf5 0%, white 50%, #ecfdf5 100%);
  border-color: #a7f3d0;
}

.other-process-item.violet {
  background: linear-gradient(135deg, #f5f3ff 0%, white 50%, #f5f3ff 100%);
  border-color: #c4b5fd;
}

.other-process-item:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.other-process-item.emerald:hover {
  border-color: #6ee7b7;
}

.other-process-item.violet:hover {
  border-color: #a78bfa;
}

.other-process-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 3rem;
  height: 3rem;
  border-radius: 0.75rem;
  margin-right: 1rem;
  transition: background-color 0.3s ease;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  flex-shrink: 0;
}

.other-process-icon.emerald {
  background-color: #d1fae5;
}

.other-process-icon.violet {
  background-color: #e9d5ff;
}

.other-process-item:hover .other-process-icon.emerald {
  background-color: #a7f3d0;
}

.other-process-item:hover .other-process-icon.violet {
  background-color: #ddd6fe;
}

.other-process-icon svg {
  width: 1.5rem;
  height: 1.5rem;
}

.other-process-icon.emerald svg {
  color: #059669;
}

.other-process-icon.violet svg {
  color: #7c3aed;
}

.other-process-content {
  flex: 1;
  min-width: 0;
}

.other-process-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.other-process-name {
  font-weight: 600;
  color: #1f2937;
  font-size: 1rem;
  transition: color 0.3s ease;
}

.other-process-item:hover .other-process-name {
  color: #111827;
}

.other-process-arrow {
  width: 1.25rem;
  height: 1.25rem;
  flex-shrink: 0;
  transition: transform 0.3s ease;
}

.other-process-arrow.emerald {
  color: var(--success-green);
}

.other-process-arrow.violet {
  color: var(--purple);
}

.other-process-item:hover .other-process-arrow {
  transform: translateX(4px);
}

.other-process-desc {
  font-size: 0.875rem;
  color: #4b5563;
  line-height: 1.625;
  transition: color 0.3s ease;
}

.other-process-item:hover .other-process-desc {
  color: #374151;
}

.other-process-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 0.75rem;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
}

.other-process-overlay.emerald {
  background: linear-gradient(to right, #d1fae5, transparent);
}

.other-process-overlay.violet {
  background: linear-gradient(to right, #e9d5ff, transparent);
}

.other-process-item:hover .other-process-overlay {
  opacity: 0.1;
}

/* 流程详情页面样式 */
.process-flow-container {
  transition: all 0.3s ease;
}

.process-flow-container:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transform: translateY(-2px);
}

.process-flow-container img {
  transition: all 0.3s ease;
}

.process-flow-container:hover img {
  transform: scale(1.02);
}

/* 职责描述列表样式 */
.responsibility-list {
  position: relative;
}

.responsibility-list li {
  position: relative;
  padding-left: 1.5rem;
  margin-bottom: 0.75rem;
  transition: all 0.3s ease;
}

.responsibility-list li:hover {
  transform: translateX(4px);
}

.responsibility-list li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0.75rem;
  width: 0.5rem;
  height: 0.5rem;
  border-radius: 50%;
  transition: all 0.3s ease;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .process-card {
    padding: 1rem;
  }
  
  .procurement-step {
    padding: 0.75rem;
  }
  
  .other-process-item {
    padding: 1rem;
  }
  
  .other-process-icon {
    width: 2.5rem;
    height: 2.5rem;
  }
  
  .other-process-icon svg {
    width: 1.25rem;
    height: 1.25rem;
  }
} 

/* 流程图查看样式 */
.process-flow-container {
  position: relative;
  transition: all 0.3s ease;
  max-height: 300px;
  overflow: hidden;
  cursor: zoom-in;
}

.process-flow-container.expanded {
  max-height: none;
  cursor: zoom-out;
}

.process-flow-container img {
  width: 100%;
  height: auto;
  object-fit: cover;
  transition: all 0.3s ease;
}

.process-flow-container:not(.expanded) img {
  max-height: 300px;
  object-position: top;
}

.process-flow-container.expanded img {
  transform: scale(1);
}

.process-flow-container:not(.expanded)::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 40px;
  background: linear-gradient(to bottom, transparent, rgba(255, 255, 255, 0.9));
  pointer-events: none;
}

.process-flow-container:not(.expanded):hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.process-flow-container:not(.expanded):hover img {
  transform: scale(1.02);
}

.expand-hint {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  background-color: rgba(255, 255, 255, 0.9);
  padding: 4px 12px;
  border-radius: 9999px;
  font-size: 0.75rem;
  color: #6b7280;
  opacity: 0;
  transition: opacity 0.3s ease;
  pointer-events: none;
  white-space: nowrap;
}

.process-flow-container:not(.expanded):hover .expand-hint {
  opacity: 1;
} 

/* Discover模块样式 - 纯CSS版本 */

/* 容器基础样式 */
.discover-container {
  display: flex;
  flex-direction: column;
  gap: 24px;
  max-width: 640px;
  margin: 0 auto;
  padding: 16px;
}

/* Hero Banner 样式 */
.hero-banner {
  position: relative;
  background: linear-gradient(to right, var(--color-blue-2), var(--color-purple), var(--color-blue-1));
  border-radius: 12px;
  padding: 24px;
  color: white;
  overflow: hidden;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

.hero-banner::before {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 128px;
  height: 128px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transform: translate(64px, -64px);
}

.hero-banner::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 96px;
  height: 96px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
  transform: translate(-48px, 48px);
}

.hero-content {
  position: relative;
  z-index: 10;
}

.hero-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 8px;
}

.hero-subtitle {
  color: #bfdbfe;
  font-size: 14px;
}

/* 新闻区域样式 */
.news-section {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
}

.section-title {
  font-size: 18px;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.section-icon {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 12px;
}

.section-icon.news-icon {
  background: #fef2f2;
}

.section-icon.knowledge-icon {
  background: #faf5ff;
}

.section-icon.quick-icon {
  background: #dbeafe;
}

.news-count {
  font-size: 12px;
  color: #6b7280;
  background: #f3f4f6;
  padding: 4px 8px;
  border-radius: 16px;
}

/* 新闻卡片样式 */
.news-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.news-card {
  position: relative;
  padding: 16px;
  border-radius: 12px;
  background: linear-gradient(to right, #eff6ff, #eef2ff);
  border: 1px solid #dbeafe;
  cursor: pointer;
  transition: all 0.3s ease;
  transform-origin: center;
}

.news-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transform: scale(1.02);
}

.news-card::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  width: 4px;
  background: var(--gradient-primary);
  border-radius: 12px 0 0 12px;
}

.news-content {
  display: flex;
  align-items: flex-start;
  gap: 12px;
}

.news-indicator {
  flex-shrink: 0;
  width: 8px;
  height: 8px;
  background: var(--light-blue);
  border-radius: 50%;
  margin-top: 8px;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.news-body {
  flex: 1;
}

.news-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.news-date {
  font-size: 12px;
  color: var(--light-blue);
  font-weight: 500;
  background: #dbeafe;
  padding: 4px 8px;
  border-radius: 16px;
}

.news-chevron {
  width: 16px;
  height: 16px;
  color: #9ca3af;
  transition: all 0.3s ease;
}

.news-card:hover .news-chevron {
  color: var(--light-blue);
  transform: translateX(4px);
}

.news-title {
  font-weight: 500;
  color: #1f2937;
  line-height: 1.6;
  transition: color 0.3s ease;
}

.news-card:hover .news-title {
  color: #1d4ed8;
}

/* 知识百科样式 */
.knowledge-section {
  background: white;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.knowledge-update {
  display: flex;
  align-items: center;
  font-size: 12px;
  color: #6b7280;
}

.knowledge-update-icon {
  width: 12px;
  height: 12px;
  margin-right: 4px;
}

.knowledge-grid {
  display: grid;
  gap: 16px;
  margin-top: 16px;
}

.knowledge-card {
  position: relative;
  padding: 20px;
  border-radius: 12px;
  border: 2px solid;
  cursor: pointer;
  transition: all 0.3s ease;
  overflow: hidden;
}

.knowledge-card::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  width: 64px;
  height: 64px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 50%;
  transform: translate(32px, -32px);
}

.knowledge-card.purple {
  background: #faf5ff;
  border-color: #e9d5ff;
}

.knowledge-card.purple:hover {
  background: #f3e8ff;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  transform: scale(1.02);
}

.knowledge-card.green {
  background: #f0fdf4;
  border-color: #bbf7d0;
}

.knowledge-card.green:hover {
  background: #dcfce7;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  transform: scale(1.02);
}

.knowledge-card.orange {
  background: #fff7ed;
  border-color: #fed7aa;
}

.knowledge-card.orange:hover {
  background: #ffedd5;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  transform: scale(1.02);
}

.knowledge-content {
  display: flex;
  align-items: flex-start;
  gap: 16px;
}

.knowledge-icon-wrapper {
  width: 48px;
  height: 48px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.knowledge-card:hover .knowledge-icon-wrapper {
  transform: scale(1.1);
}

.knowledge-icon-wrapper.purple {
  background: linear-gradient(to bottom right, var(--color-purple), var(--color-pink));
}

.knowledge-icon-wrapper.green {
  background: var(--gradient-success);
}

.knowledge-icon-wrapper.orange {
  background: var(--gradient-warning);
}

.knowledge-icon {
  width: 24px;
  height: 24px;
  color: white;
}

.knowledge-text {
  flex: 1;
}

.knowledge-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.knowledge-title {
  font-weight: bold;
  color: #1f2937;
  transition: color 0.3s ease;
  margin: 0;
}

.knowledge-chevron {
  width: 20px;
  height: 20px;
  color: #9ca3af;
  transition: all 0.3s ease;
}

.knowledge-card:hover .knowledge-chevron {
  color: #6b7280;
  transform: translateX(4px);
}

.knowledge-desc {
  font-size: 14px;
  color: #6b7280;
  line-height: 1.6;
  margin: 0;
}

/* 快速入口样式 */
.quick-actions {
  background: linear-gradient(to right, #f9fafb, #eff6ff);
  border-radius: 12px;
  padding: 24px;
  border: 1px solid #e5e7eb;
}

.quick-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 12px;
  margin-top: 16px;
}

.quick-action {
  background: white;
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  cursor: pointer;
  transition: all 0.3s ease;
}

.quick-action:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transform: scale(1.05);
}

.quick-content {
  display: flex;
  align-items: center;
  gap: 12px;
}

.quick-icon-wrapper {
  width: 32px;
  height: 32px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.3s ease;
}

