}

.factor-category.category-客户导向 {
  background: rgba(40, 150, 215, 0.1);
  color: var(--color-blue-1);
}

.factor-category.category-团队合作 {
  background: rgba(0, 211, 127, 0.1);
  color: var(--color-green-1);
}

.factor-category.category-能力发展 {
  background: rgba(188, 125, 255, 0.1);
  color: var(--color-purple);
}

.factor-status-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 0.5rem;
}

.factor-status {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.375rem 0.75rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
}

.factor-status.status-completed {
  background: var(--success-bg);
  color: var(--success-green);
}

.factor-status.status-inprogress {
  background: var(--warning-bg);
  color: var(--warning-yellow);
}

.factor-progress {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  min-width: 120px;
}

.progress-text {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--gray-700);
  min-width: 35px;
}

/* 因子内容区域 */
.factor-section {
  margin-bottom: 1rem;
  padding: 1rem;
  background: var(--gray-50);
  border-radius: 0.75rem;
  border-left: 4px solid var(--color-blue-1);
}

.section-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.75rem;
}

.section-icon {
  width: 1rem;
  height: 1rem;
  color: var(--color-blue-1);
}

.section-title {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--gray-700);
}

.section-content {
  font-size: 0.875rem;
  line-height: 1.5;
  color: var(--gray-600);
  margin: 0;
}

/* 评分区域 */
.factor-ratings {
  display: flex;
  gap: 1.5rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
}

.rating-item {
  flex: 1;
  min-width: 200px;
}

.rating-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.rating-icon {
  width: 1rem;
  height: 1rem;
  color: var(--color-blue-1);
}

.rating-label {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--gray-700);
}

.rating-stars {
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.star {
  width: 1.125rem;
  height: 1.125rem;
  transition: all 0.2s ease;
}

.star-filled {
  color: var(--color-yellow);
  fill: var(--color-yellow);
}

.star-empty {
  color: var(--gray-300);
  fill: none;
}

.rating-score {
  margin-left: 0.5rem;
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--gray-700);
}

.rating-pending {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  color: var(--gray-500);
  font-size: 0.875rem;
}

/* 操作按钮 */
.factor-actions {
  display: flex;
  gap: 0.75rem;
  justify-content: flex-end;
  flex-wrap: wrap;
}

.action-btn {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  border: 1px solid;
  cursor: pointer;
  transition: all 0.3s ease;
}

.action-btn-edit {
  background: var(--white);
  color: var(--color-blue-1);
  border-color: var(--color-blue-1);
}

.action-btn-edit:hover {
  background: var(--color-blue-1);
  color: var(--white);
}

.action-btn-view {
  background: var(--white);
  color: var(--gray-600);
  border-color: var(--gray-300);
}

.action-btn-view:hover {
  background: var(--gray-100);
  color: var(--gray-700);
}

/* 移动端适配 */
@media (max-width: 768px) {
  .success-factor-header {
    flex-direction: column;
    align-items: stretch;
  }
  
  .factor-status-section {
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .factor-ratings {
    flex-direction: column;
    gap: 1rem;
  }
  
  .rating-item {
    min-width: auto;
  }
  
  .factor-actions {
    justify-content: stretch;
  }
  
  .action-btn {
    flex: 1;
