/* === KPI "10选4" 动态选择功能样式 === */

/* 选择模式头部控件 - 计数器居中布局 */
.kpi-header-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

/* 选择计数器 - 独占一行居中显示 */
.selection-counter {
  background: var(--gradient-primary);
  color: white;
  padding: 0.875rem 2rem;
  border-radius: 1.5rem;
  font-size: 1.25rem;
  font-weight: 700;
  text-align: center;
  box-shadow: 0 6px 16px rgba(40, 150, 215, 0.4);
  position: relative;
  z-index: 10;
  letter-spacing: 0.05em;
  min-width: 120px;
}

/* 选择状态容器 - 仅包含计数器 */
.selection-status {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.reselect-button {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.5rem 0.75rem;
  background: var(--gray-100);
  color: var(--gray-700);
  border: none;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  cursor: pointer;
  transition: all 0.3s ease;
}

.reselect-button:hover {
  background: var(--gray-200);
}

/* 选择提示区域 */
.selection-hint {
  background: linear-gradient(135deg, var(--rexel-blue-bg), var(--info-bg));
  border: 1px solid var(--rexel-blue-hover);
  border-radius: 0.5rem;
  padding: 0.75rem;
  margin-bottom: 0.75rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.hint-text {
  color: var(--gray-700);
  font-size: 0.875rem;
  line-height: 1.5;
}

.confirm-selection-button {
  padding: 1rem 2rem;
  border-radius: 1rem;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
  width: 100%;
  min-height: 48px;
}

.selection-hint {
  flex-direction: column;
  gap: 1rem;
  text-align: center;
}

.hint-text {
  font-size: 1rem;
  line-height: 1.6;
}

.confirm-selection-button.active {
  background: var(--gradient-success);
  color: white;
}

.confirm-selection-button.active:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(0, 185, 91, 0.3);
}

.confirm-selection-button.disabled {
  background: var(--gray-200);
  color: var(--gray-500);
  cursor: not-allowed;
}

/* KPI选择状态 - 统一为单列移动端布局 */
.kpi-list.selection-mode {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  max-width: 100%;
}

.kpi-list.claimed-mode {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  max-width: 100%;
}

.kpi-item.kpi-selected {
  border-color: var(--success-green);
  box-shadow: 0 0 0 3px var(--success-green), 0 12px 20px -3px rgba(34, 197, 94, 0.4);
  transform: translateY(-3px);
}

.kpi-item.kpi-selected .kpi-card-blue {
  background: linear-gradient(135deg, rgba(34, 197, 94, 0.15) 0%, rgba(40, 150, 215, 0.08) 100%) !important;
  border-color: var(--success-green) !important;
}

.kpi-item.kpi-selected .kpi-card-green {
  background: linear-gradient(135deg, rgba(34, 197, 94, 0.15) 0%, rgba(0, 211, 127, 0.08) 100%) !important;
  border-color: var(--success-green) !important;
}

.kpi-item.kpi-disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.kpi-item.kpi-disabled:hover {
  transform: none;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

/* 选择控件 - 统一为移动端尺寸 */
.kpi-indicator {
  position: absolute;
  top: 1rem;
  right: 1rem;
  z-index: 10;
}

.selection-controls {
  display: flex;
  align-items: center;
  justify-content: center;
}

.selection-checkbox {
  width: 1.75rem;
  height: 1.75rem;
  border: 2px solid var(--gray-300);
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  transition: all 0.3s ease;
}

.selection-checkbox.selected {
  background: var(--gradient-success);
  border-color: var(--success-green);
  color: white;
}

/* 分类标签 - 统一为移动端尺寸 */
.category-tag {
  position: absolute;
  top: 1rem;
  left: 1rem;
  padding: 0.375rem 0.75rem;
  border-radius: 0.75rem;
  font-size: 0.875rem;
  font-weight: 500;
  z-index: 5;
}

.category-tag.category-效率 {
  background: rgba(40, 150, 215, 0.1);
  color: var(--color-blue-1);
}

.category-tag.category-成本 {
  background: rgba(0, 185, 91, 0.1);
  color: var(--color-green-2);
}

.category-tag.category-质量 {
  background: rgba(188, 125, 255, 0.1);
  color: var(--color-purple);
}

.category-tag.category-业务量 {
  background: rgba(255, 167, 0, 0.1);
  color: var(--color-orange);
}

.category-tag.category-服务 {
  background: rgba(255, 109, 255, 0.1);
  color: var(--color-pink);
}

.category-tag.category-创新 {
  background: rgba(204, 255, 0, 0.1);
  color: var(--color-lime);
}

.category-tag.category-管理 {
  background: rgba(255, 245, 0, 0.1);
  color: var(--color-yellow);
}

/* 选择覆盖层 */
.selection-overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7), transparent);
  border-radius: 0 0 1rem 1rem;
  padding: 0.5rem;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.kpi-item:hover .selection-overlay {
  opacity: 1;
}

.selection-hint {
  color: white;
  font-size: 0.75rem;
  text-align: center;
  font-weight: 600;
}

/* KPI底部区域 */
.kpi-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.5rem;
}

.priority-indicator {
  padding: 0.125rem 0.375rem;
  border-radius: 0.25rem;
  font-size: 0.625rem;
  font-weight: 500;
}

.priority-indicator.priority-high {
  background: rgba(239, 68, 68, 0.1);
  color: #dc2626;
}

.priority-indicator.priority-medium {
  background: var(--warning-bg);
  color: var(--warning-yellow);
}

.priority-indicator.priority-low {
  background: var(--purple-bg);
  color: var(--purple);
}

/* === 原有KPI认领动态功能样式 === */

/* 认领状态指示器 */
.kpi-claim-indicator {
  position: absolute;
  top: 0.75rem;
  right: 0.75rem;
  z-index: 10;
}

.claim-badge {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.375rem 0.75rem;
  border-radius: 1rem;
  font-size: 0.75rem;
  font-weight: 600;
  transition: all 0.3s ease;
}

.claim-badge.claimed {
  background: var(--gradient-success);
  color: white;
  animation: claim-success 0.6s ease-out;
}

.claim-badge.claiming {
  background: var(--gradient-primary);
  color: white;
}

/* 优先级标签 */
.priority-tag {
  padding: 0.25rem 0.5rem;
  border-radius: 0.5rem;
  font-size: 0.75rem;
  font-weight: 500;
}

.priority-tag.priority-high {
  background: rgba(239, 68, 68, 0.1);
  color: #dc2626;
}

.priority-tag.priority-medium {
  background: var(--warning-bg);
  color: var(--warning-yellow);
}

.priority-tag.priority-low {
  background: var(--purple-bg);
  color: var(--purple);
}

/* 认领旋转动画 */
.claim-spinner {
  width: 1rem;
  height: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes claim-success {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* 截止日期样式 */
.kpi-deadline {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.625rem;
  color: var(--gray-500);
  margin-bottom: 0.375rem;
}

/* 认领按钮 */
.claim-button {
  display: flex;
  align-items: center;
  gap: 0.375rem;
  padding: 0.5rem 0.75rem;
  background: var(--gradient-primary);
  color: white;
  border: none;
  border-radius: 0.5rem;
  font-size: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.claim-button:hover {
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(18, 70, 206, 0.3);
}

.claim-button:active {
  transform: translateY(0);
}

/* KPI操作区域 */
.kpi-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.75rem;
}

/* KPI项目状态 */
.kpi-item.kpi-claiming {
  animation: claiming-pulse 2s ease-in-out infinite;
}

.kpi-item.kpi-claimed {
  border-color: var(--success-green);
  box-shadow: 0 0 0 1px var(--success-green), 0 10px 15px -3px rgba(0, 0, 0, 0.1);
}

.kpi-item.kpi-claimed::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(0, 185, 91, 0.05), transparent);
  border-radius: 1rem;
  pointer-events: none;
}

@keyframes claiming-pulse {
  0%, 100% {
    box-shadow: 0 0 0 0 rgba(18, 70, 206, 0.4), 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  }
  50% {
    box-shadow: 0 0 0 8px rgba(18, 70, 206, 0), 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  }
}

