}

.trend-label-green {
  font-size: 1rem; /* 增大趋势标签字体 */
  font-weight: 500;
}

.trend-value-blue {
  font-size: 1.75rem; /* 增大趋势值字体 */
  font-weight: 700;
  color: #1d4ed8;
}

.trend-value-green {
  font-size: 1.75rem; /* 增大趋势值字体 */
  font-weight: 700;
  color: #15803d;
}

.trend-icon-blue {
  width: 2rem;
  height: 2rem;
  color: var(--light-blue);
}

.trend-icon-green {
  width: 2rem;
  height: 2rem;
  color: #16a34a;
}

/* 行动建议 */
.suggestions-container {
  background: linear-gradient(90deg, #fff7ed 0%, #fef2f2 100%);
  border-radius: 0.75rem;
  padding: 1.5rem;
  border: 1px solid #fed7aa;
}

.suggestions-title {
  font-size: 1.375rem; /* 增大建议标题字体 */
  font-weight: 600;
  margin-bottom: 0.75rem;
  display: flex;
  align-items: center;
  color: #9a3412;
}

.suggestions-title-icon {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.5rem;
}

.suggestions-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.suggestion-item {
  display: flex;
  align-items: center; /* 修改为 center，使点和文字在同一水平线上 */
  gap: 0.5rem;
}

.suggestion-dot {
  width: 0.5rem;
  height: 0.5rem;
  background-color: #fb923c;
  border-radius: 9999px;
  margin-top: 0; /* 移除上边距，因为现在用 align-items: center 对齐 */
  flex-shrink: 0;
}

.suggestion-text {
  font-size: 1rem; /* 增大建议文本字体 */
  color: #c2410c;
  line-height: 1.5; /* 添加行高，使文字更易读 */
}

/* 动画关键帧 */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

/* 个人信息模块美化CSS样式 */

/* 容器基础样式 */
.personal-info-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

/* 个人基本信息卡片 */
.personal-basic-info {
  background: linear-gradient(135deg, #dbeafe 0%, #e0e7ff 100%);
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border: 1px solid #dbeafe;
}

.personal-basic-info h3 {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  display: flex;
  align-items: center;
  color: #4338ca;
}

.personal-basic-info .icon {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.5rem;
}

.info-items {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.info-item {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  background-color: white;
  border-radius: 0.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  transition: box-shadow 0.15s ease-in-out;
}

.info-item:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.info-icon-wrapper {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1rem;
}

.info-icon-wrapper.mail {
  background-color: var(--blue-hover);
}

.info-icon-wrapper.mail .icon {
  color: var(--light-blue);
}

.info-icon-wrapper.building {
  background-color: #dcfce7;
}

.info-icon-wrapper.building .icon {
  color: #16a34a;
}

.info-content .label {
  font-size: 0.875rem;
  color: #6b7280;
}

.info-content .value {
  font-weight: 500;
  color: #1f2937;
}

/* 工作概览卡片 */
.work-overview {
  background-color: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.work-overview h3 {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  color: #374151;
}

.work-overview .icon {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.5rem;
  color: #7c3aed;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.stat-item {
  padding: 1rem;
  border-radius: 0.5rem;
  text-align: center;
}

.stat-item.purple {
  background: linear-gradient(135deg, #faf5ff 0%, #fdf2f8 100%);
}

.stat-item.green {
  background: linear-gradient(135deg, #f0fdf4 0%, #ecfdf5 100%);
}

.stat-item.blue {
  background: linear-gradient(135deg, #eff6ff 0%, #ecfeff 100%);
}

.stat-item.orange {
  background: linear-gradient(135deg, #fff7ed 0%, #fefce8 100%);
}

.stat-number {
  font-size: 1.5rem;
  font-weight: 700;
}

.stat-number.purple {
  color: #7c3aed;
}

.stat-number.green {
  color: #16a34a;
}

.stat-number.blue {
  color: var(--light-blue);
}

.stat-number.orange {
  color: #ea580c;
}

/* 今日概览中的 stat-label 保持白色，其他地方的 stat-label 使用灰色 */
.overview-stats .stat-label {
  font-size: 0.875rem;
  color: white;
  font-weight: 500;
}

/* 其他模块的 stat-label */
.stat-label {
  font-size: 0.875rem;
  color: #6b7280;
}

/* 快速操作卡片 */
.quick-actions {
  background-color: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.quick-actions h3 {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  color: #374151;
}

.quick-actions .title-icon {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.5rem;
  color: #4f46e5;
}

.actions-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.75rem;
}

.action-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  color: white;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.action-button:hover {
  transform: scale(1.05);
}

.action-button.blue {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
}

.action-button.blue:hover {
  background: linear-gradient(135deg, #2563eb 0%, #1d4ed8 100%);
}

.action-button.green {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
}

.action-button.green:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
}

.action-button.purple {
  background: linear-gradient(135deg, #8b5cf6 0%, #7c3aed 100%);
}

.action-button.purple:hover {
  background: linear-gradient(135deg, #7c3aed 0%, #6d28d9 100%);
}

.action-button.orange {
  background: linear-gradient(135deg, #f97316 0%, #ea580c 100%);
}

.action-button.orange:hover {
  background: linear-gradient(135deg, #ea580c 0%, #c2410c 100%);
}

.action-button .icon {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.5rem;
}

.action-button .text {
  font-size: 0.875rem;
  font-weight: 500;
}

/* 最近活动卡片 */
.recent-activities {
  background-color: white;
  border-radius: 0.75rem;
  padding: 1.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.recent-activities h3 {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  color: #374151;
}

.recent-activities .title-icon {
  width: 1.25rem;
  height: 1.25rem;
  margin-right: 0.5rem;
  color: #16a34a;
}

.activities-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.activity-item {
  display: flex;
  align-items: center;
  padding: 0.75rem;
  background-color: #f9fafb;
  border-radius: 0.5rem;
  transition: background-color 0.15s ease-in-out;
  cursor: pointer;
}

.activity-item:hover {
  background-color: #f3f4f6;
}

.activity-icon-wrapper {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.75rem;
}

.activity-icon-wrapper.blue {
  background-color: var(--blue-hover);
}

.activity-icon-wrapper.blue .icon {
  color: var(--light-blue);
}

.activity-icon-wrapper.green {
  background-color: #dcfce7;
}

.activity-icon-wrapper.green .icon {
  color: #16a34a;
}

.activity-icon-wrapper.purple {
  background-color: #f3e8ff;
}

.activity-icon-wrapper.purple .icon {
  color: #7c3aed;
}

.activity-icon-wrapper .icon {
  width: 1rem;
  height: 1rem;
}

.activity-content {
  flex: 1;
}

.activity-title {
  font-weight: 500;
  font-size: 0.875rem;
}

.activity-time {
  font-size: 0.75rem;
  color: #6b7280;
}

/* 响应式设计 */
@media (max-width: 768px) {
  .stats-grid,
  .actions-grid {
    grid-template-columns: 1fr;
  }
  
  .personal-basic-info,
  .work-overview,
  .quick-actions,
  .recent-activities {
    padding: 1rem;
  }
}